.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
  overflow: hidden;
}

.modal_container {
  position: fixed;
  top: 50%;
  left: 50%;
  padding: 40px;
  max-width: 540px;
  min-height: 464px;
  width: 100%;
  transform: scale(1) translate(-50%, -50%);
}
